*{
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
#navigation{
  width: 100%;
  display:flex;
  flex-wrap: wrap;
  height: 10vh;
  background-color: #D71E3C;
}

.images{
  width: 40%;
  display: flex;
  flex-wrap: wrap;
  margin-left: 10%;
  float: left;
}
@media screen and (max-width:610px){
  .images{
    width: 100%;
    margin-left:auto;
    margin-right: auto;
    text-align: center;
  }
}
.connect{
  display: flex;
  float: left;
}
